// =============================================================================
// WEBHOOK CONFIGURATIONS DATABASE
// =============================================================================
// Database operations for webhook configurations

import { executeQuery, executeQueryOne, executeUpdate } from './query-helper';
import type { WebhookConfig } from '@/lib/webhook-delivery';

export interface WebhookConfigRow {
  id: string;
  api_key_id: string;
  endpoint_id: string | null;
  url: string;
  secret: string;
  events: string[]; // JSON array
  enabled: boolean;
  created_at: string;
  updated_at: string;
}

/**
 * Convert database row to WebhookConfig
 */
function rowToConfig(row: WebhookConfigRow): WebhookConfig {
  return {
    id: row.id,
    endpointId: row.endpoint_id || undefined,
    url: row.url,
    secret: row.secret,
    events: row.events as any,
    enabled: row.enabled,
    createdAt: row.created_at,
    updatedAt: row.updated_at,
  };
}

/**
 * Get all webhook configurations
 */
export async function getWebhookConfigs(filters?: {
  apiKeyId?: string;
  endpointId?: string;
  enabled?: boolean;
}): Promise<WebhookConfig[]> {
  // Determine if we're using PostgreSQL (uses $1, $2) or SQLite (uses ?)
  const { getDb } = await import('./client');
  const db = getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  
  let query = 'SELECT * FROM webhook_configs WHERE 1=1';
  const params: any[] = [];
  let paramIndex = 1;

  if (filters?.apiKeyId) {
    if (isPostgres) {
      query += ` AND api_key_id = $${paramIndex++}`;
    } else {
      query += ' AND api_key_id = ?';
    }
    params.push(filters.apiKeyId);
  }
  if (filters?.endpointId) {
    if (isPostgres) {
      query += ` AND endpoint_id = $${paramIndex++}`;
    } else {
      query += ' AND endpoint_id = ?';
    }
    params.push(filters.endpointId);
  }
  if (filters?.enabled !== undefined) {
    if (isPostgres) {
      query += ` AND enabled = $${paramIndex++}`;
    } else {
      query += ' AND enabled = ?';
    }
    params.push(filters.enabled ? (isPostgres ? true : 1) : (isPostgres ? false : 0));
  }

  query += ' ORDER BY created_at DESC';

  const rows = await executeQuery<WebhookConfigRow>(query, params);
  return rows.map(row => ({
    ...rowToConfig(row),
    events: typeof row.events === 'string' ? JSON.parse(row.events) : (Array.isArray(row.events) ? row.events : []),
  }));
}

/**
 * Get webhook configuration by ID
 */
export async function getWebhookConfig(id: string): Promise<WebhookConfig | null> {
  const { getDb } = await import('./client');
  const db = getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  const query = isPostgres 
    ? 'SELECT * FROM webhook_configs WHERE id = $1'
    : 'SELECT * FROM webhook_configs WHERE id = ?';
  
  const row = await executeQueryOne<WebhookConfigRow>(query, [id]);
  if (!row) return null;
  
  return {
    ...rowToConfig(row),
    events: typeof row.events === 'string' ? JSON.parse(row.events) : (Array.isArray(row.events) ? row.events : []),
  };
}

/**
 * Create webhook configuration
 */
export async function createWebhookConfig(config: Omit<WebhookConfig, 'id' | 'createdAt' | 'updatedAt'> & {
  id?: string;
  apiKeyId: string;
}): Promise<WebhookConfig> {
  const id = config.id || `wh_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
  const now = new Date().toISOString();

  const { getDb } = await import('./client');
  const db = getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  
  const query = isPostgres
    ? `INSERT INTO webhook_configs (id, api_key_id, endpoint_id, url, secret, events, enabled, created_at, updated_at)
       VALUES ($1, $2, $3, $4, $5, $6, $7, $8, $9)`
    : `INSERT INTO webhook_configs (id, api_key_id, endpoint_id, url, secret, events, enabled, created_at, updated_at)
       VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?)`;
  
  const params = isPostgres
    ? [
        id,
        config.apiKeyId,
        config.endpointId || null,
        config.url,
        config.secret,
        JSON.stringify(config.events),
        config.enabled !== false,
        now,
        now,
      ]
    : [
        id,
        config.apiKeyId,
        config.endpointId || null,
        config.url,
        config.secret,
        JSON.stringify(config.events),
        config.enabled !== false ? 1 : 0,
        now,
        now,
      ];

  await executeUpdate(query, params);

  return {
    id,
    endpointId: config.endpointId,
    url: config.url,
    secret: config.secret,
    events: config.events,
    enabled: config.enabled !== false,
    createdAt: now,
    updatedAt: now,
  };
}

/**
 * Update webhook configuration
 */
export async function updateWebhookConfig(
  id: string,
  updates: Partial<Pick<WebhookConfig, 'url' | 'events' | 'enabled'>>
): Promise<WebhookConfig | null> {
  const { getDb } = await import('./client');
  const db = getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  const now = new Date().toISOString();

  const setClauses: string[] = [];
  const params: any[] = [];
  let paramIndex = 1;

  setClauses.push(isPostgres ? `updated_at = $${paramIndex++}` : 'updated_at = ?');
  params.push(now);

  if (updates.url !== undefined) {
    setClauses.push(isPostgres ? `url = $${paramIndex++}` : 'url = ?');
    params.push(updates.url);
  }
  if (updates.events !== undefined) {
    setClauses.push(isPostgres ? `events = $${paramIndex++}` : 'events = ?');
    params.push(JSON.stringify(updates.events));
  }
  if (updates.enabled !== undefined) {
    setClauses.push(isPostgres ? `enabled = $${paramIndex++}` : 'enabled = ?');
    params.push(updates.enabled ? (isPostgres ? true : 1) : (isPostgres ? false : 0));
  }

  params.push(id);
  const whereClause = isPostgres ? `$${paramIndex}` : '?';
  const query = `UPDATE webhook_configs SET ${setClauses.join(', ')} WHERE id = ${whereClause}`;

  await executeUpdate(query, params);

  return getWebhookConfig(id);
}

/**
 * Delete webhook configuration
 */
export async function deleteWebhookConfig(id: string): Promise<boolean> {
  const { getDb } = await import('./client');
  const db = getDb();
  const isPostgres = 'pool' in db || typeof (db as any).pool?.query === 'function';
  const query = isPostgres
    ? 'DELETE FROM webhook_configs WHERE id = $1'
    : 'DELETE FROM webhook_configs WHERE id = ?';
  
  const result = await executeUpdate(query, [id]);
  return (result.rowCount ?? result.changes ?? 0) > 0;
}

