// =============================================================================
// METERED ENDPOINTS DATABASE
// =============================================================================
// Database layer for metered endpoints, payments, and usage tracking
// Uses adapter pattern: SQLite (dev) or PostgreSQL (prod)

import { getDb } from './client';
import type { DatabaseAdapter } from './adapters/base-adapter';

// Types
export interface MeteredEndpoint {
  id: string;
  name: string;
  description?: string;
  upstreamUrl: string;
  price: string; // Price in smallest unit (e.g., 1000000 for 1 USDC)
  network: string; // 'base', 'base-sepolia', etc.
  tokenAddress: string; // USDC address
  tokenSymbol: string; // 'USDC'
  chainId: number;
  recipientAddress: string; // Address that receives payments (must not be blacklisted)
  status: 'active' | 'paused' | 'deleted';
  createdAt: string;
  updatedAt: string;
  createdBy?: string; // User/API key that created it
}

export interface Payment {
  id: string;
  endpointId: string;
  txHash: string;
  fromAddress: string;
  toAddress: string;
  amount: string;
  tokenAddress: string;
  network: string;
  verifiedAt: string;
  kytStatus?: 'passed' | 'flagged' | 'blocked';
  ofacStatus?: 'passed' | 'flagged' | 'blocked';
  facilitator: string; // 'cdp', 'payai', etc.
}

export interface UsageLog {
  id: string;
  endpointId: string;
  paymentId?: string; // Link to payment if payment was required
  timestamp: string;
  method: string; // HTTP method
  path: string;
  statusCode: number;
  responseTime: number; // milliseconds
  units: number; // Usage units (could be 1 per call, or based on response size, etc.)
  ipAddress?: string;
  userAgent?: string;
  // Observability fields
  conversationId?: string; // Conversation/request ID for tracing
  agentId?: string; // Agent ID that made the request
  x402TxHash?: string; // x402 transaction hash
  failureCode?: string; // Error code if request failed
}

// =============================================================================
// ENDPOINTS CRUD
// =============================================================================

export async function createEndpoint(
  endpoint: Omit<MeteredEndpoint, 'id' | 'createdAt' | 'updatedAt'> & { id?: string }
): Promise<MeteredEndpoint> {
  const db = getDb();
  return await db.createEndpoint(endpoint);
}

export async function getEndpoint(endpointId: string): Promise<MeteredEndpoint | null> {
  const db = getDb();
  return await db.getEndpoint(endpointId);
}

export async function listEndpoints(filters?: { status?: string; createdBy?: string }): Promise<MeteredEndpoint[]> {
  const db = getDb();
  return await db.listEndpoints(filters);
}

export async function updateEndpoint(
  endpointId: string,
  updates: Partial<MeteredEndpoint>
): Promise<MeteredEndpoint | null> {
  const db = getDb();
  return await db.updateEndpoint(endpointId, updates);
}

export async function deleteEndpoint(endpointId: string): Promise<boolean> {
  const db = getDb();
  return await db.deleteEndpoint(endpointId);
}

// =============================================================================
// PAYMENTS CRUD
// =============================================================================

export async function createPayment(payment: Omit<Payment, 'id' | 'verifiedAt'>): Promise<Payment> {
  const db = getDb();
  return await db.createPayment(payment);
}

export async function getPayment(paymentId: string): Promise<Payment | null> {
  const db = getDb();
  return await db.getPayment(paymentId);
}

export async function getPaymentByTxHash(txHash: string): Promise<Payment | null> {
  const db = getDb();
  return await db.getPaymentByTxHash(txHash);
}

export async function listPayments(filters?: { endpointId?: string; fromAddress?: string }): Promise<Payment[]> {
  const db = getDb();
  return await db.listPayments(filters);
}

export async function getPaymentsForEndpoint(endpointId: string, limit: number = 100): Promise<Payment[]> {
  const db = getDb();
  return await db.getPaymentsForEndpoint(endpointId, limit);
}

// =============================================================================
// USAGE LOGS CRUD
// =============================================================================

export async function createUsageLog(log: Omit<UsageLog, 'id' | 'timestamp'>): Promise<UsageLog> {
  const db = getDb();
  return await db.createUsageLog(log);
}

export async function listUsageLogs(filters?: { endpointId?: string; paymentId?: string }): Promise<UsageLog[]> {
  const db = getDb();
  return await db.listUsageLogs(filters);
}

export async function getUsageStats(
  endpointId: string,
  startDate?: string,
  endDate?: string
): Promise<{
  totalRequests: number;
  totalPayments: number;
  totalRevenue: string;
  averageResponseTime: number;
}> {
  const db = getDb();
  return await db.getUsageStats(endpointId, startDate, endDate);
}
